function generateAuthError(message) {
    switch (message) {
    case 'INVALID_PASSWORD':
        return 'Email    '
    case 'EMAIL_EXISTS':
        return '   Email  '
    default:
        if (message.indexOf('TOO_MANY_ATTEMPTS_TRY_LATER') === 0) {
            return '   .  '
        }
    }
}

export default generateAuthError
